<template>
  <div id="app">
      <el-container>
      <el-aside  width="">
        <appmenu :isCollapse=isCollapse></appmenu>
      </el-aside>
      <el-container>
        <el-header class="height_center">
            <i class="el-icon-arrow-left menubutton" v-if="isCollapse==false" @click="handelmenu()"></i>
            <i class="el-icon-arrow-right menubutton" v-if="isCollapse==true" @click="handelmenu()"></i>
            <breadcrumd></breadcrumd>
            <div class="img-box">
              <img src="../src/assets/touxiang.jpg" class="user-img">
            </div>
        </el-header>
        <el-main><router-view /></el-main>
      </el-container>
    </el-container>

  </div>
</template>

<script>
  import store from "../src/store/tags"
  import breadcrumd from "./components/breadcrumd";
  import appmenu from "./components/menu";
export default {
  name: "App",
  data(){
    return {
    }
  },computed:{
    isCollapse() {
      return store.state.isCollapse;
    },
  },store,
  components:{breadcrumd,appmenu},
  mounted(){
  },
  methods:{
  handelmenu(){
    store.commit('changeCollapse');
  },

}
}
</script>

<style>
  .img-box{
    position: absolute;
    width:3rem;
    height:3rem;
    overflow: hidden;
    border-radius: 50%;
    right:3rem;
  }
  .user-img{
    width:3rem;
    height:3rem;
    cursor: pointer;
    position: relative;
      transition: all 0.5s;
  }
  .user-img:hover{
    transform:scale(1.2,1.2);
  } 
  .el-menu-vertical-demo{
    border:0px;
  }
  html,body{
    padding: 0;
    margin: 0;
  }
  .head{
    height: 100%;
    width:100%;
  }
  .el-container{
    height: 100%;
  }
  .height_center{
    display: flex;
    align-items: center;
    background-color: rgb(34, 35, 36);
  }
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100vh;
  width:100%;
}
a{
  text-decoration: none;
}
.menubutton{
  font-size: 1.6rem;
  float: left;
  margin-right:1rem;
  color: #fff;
}
.el-menu:not(.el-menu--collapse) {
      width: 200px;
    }
</style>



