<template>
  <el-menu
    :default-active="activeindex"
    class="el-menu-vertical-demo"
    @open="handleOpen"
    @close="handleClose"
    :collapse="isCollapse"
    :collapse-transition="true"
    background-color="#545c64"
    text-color="#fff"
    style="height:100%"
  >
    <router-link to="/" class="menu-text">
      <el-menu-item index="1">
        <i class="el-icon-s-home"></i>
        <span slot="title">首页</span>
      </el-menu-item>
    </router-link>
    <router-link to="/baseinfo" class="menu-text">
      <el-menu-item index="2">
        <i class="el-icon-user"></i>
        <span slot="title">个人基本信息</span>
      </el-menu-item>
    </router-link>
    <router-link to="/projectshow" class="menu-text">
      <el-submenu index="3">
        <template slot="title">
          <i class="el-icon-menu"></i>
          <span slot="title">{{ isCollapse ? "" : "Demo" }}</span>
        </template>
        <el-submenu index="3-0">
          <template slot="title">Canvans</template>
          <el-menu-item-group>
          <router-link to="/snake">
            <el-menu-item index="3-1">贪吃蛇</el-menu-item></router-link>
          <router-link to="/consume">
            <el-menu-item index="3-2">连连看</el-menu-item></router-link>
               <router-link to="/audio_visualization">
            <el-menu-item index="3-3">音频可视化</el-menu-item></router-link
          ></el-menu-item-group>
        </el-submenu>
        <el-submenu index="3-1">
          <template slot="title">three.js_demo</template>
          <el-menu-item-group>
          <router-link to="/threedemo">
            <el-menu-item index="3-1">three.js_demo</el-menu-item></router-link>
   </el-menu-item-group>
        </el-submenu>
        <el-submenu index="3-2">
          <template slot="title">css_demo</template>
          <el-menu-item-group>
          <router-link to="/button">
            <el-menu-item index="3-1">按钮样例</el-menu-item></router-link>
   </el-menu-item-group>
        </el-submenu>
      </el-submenu>
    </router-link>
    <router-link to="/custom_component" class="menu-text">
      <el-submenu index="4">
        <template slot="title">
          <i class="el-icon-menu"></i>
          <span slot="title">{{ isCollapse ? "" : "自定义组件" }}</span>
        </template>
          <el-menu-item-group>
          <router-link to="/customize_table">
            <el-menu-item index="4-1">自定义表格</el-menu-item></router-link>
         </el-menu-item-group>
      </el-submenu>
    </router-link>

  </el-menu>
</template>
<script>
import store from "../store/tags";
import { mapState } from "vuex";
import {indexarr} from "../json_config/menu.js"
export default {
  data() {
    return {
      //设置默认访问的路由
      activeindex: "1",
      //设置菜单路由,方便路由改变时,改变相应的状态
      indexarr: indexarr
    };
  },
  methods: {
    handleOpen(key, keyPath) {
      // console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      // console.log(key, keyPath);
    },
    handelactiveindex() {
      let index = this.indexarr.findIndex(item => {
        return this.currentMenu.name == item.name;
      });
      this.activeindex = this.indexarr[index].index;
    }
  },
  props: {
    isCollapse: Boolean
  },
  computed: {
    currentMenu() {
      return store.state.currentMenu;
    }
  },
  watch: {
    currentMenu(newData, oldData) {
     this.handelactiveindex();
    }
  },
  created() {
    window.onclick = this.handelactiveindex;
  }
};
</script>
<style scoped></style>



