<template>
  <div class="content">
    <h2>欢迎来到我的网站</h2>
    <div class="table">
      <access_acle v-if="dataready" :option="access"></access_acle>
      <div class="webside">
        <div  class="titletext">本网站主要用作面试项目展示</div>
        <div class="prjecttext">应用技术栈:vue2全家桶,express,element-ui,canvans的应用,echars的简单运用</div>
        <div  class="titletext">主要实现功能:</div>
        <div class="prjecttext">利用<span class="importent">express</span>搭建简单的后台,用于统计访问量以及记录小游戏的得分</div>
        <div class="prjecttext">利用<span class="importent">canvas</span>实现简单的贪吃蛇,连连看小游戏</div>
        <div class="prjecttext">利用路由守卫,<span class="importent">vuex</span>实现动态路由标签</div>
        <div class="prjecttext">按钮交互动效设计</div>
        <div class="prjecttext"><span class="importent">element-ui</span>组件的封装,<span class="importent">threejs</span>3D的简单案例,音频可视化简单的demo</div>
      
      </div>
    </div>
  </div>
</template>
<script>
import access_acle from "../components/access_scale";
import axios from "axios";
export default {
  data() {
    return {
      access: {
        title: {
          text: "站点访问统计",
          subtext: "站点后台统计",
          left: "center"
        },
        tooltip: {
          trigger: "item"
        },
        legend: {
          orient: "vertical",
          left: "left"
        },
        series: [
          {
            name: "Access From",
            type: "pie",
            radius: "50%",
            data: [
              { value: null, name: "历史访问量" },
              { value: null, name: "本周访问量" },
              { value: null, name: "今日访问量" }
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      },dataready:false
    };
  },
  components: { access_acle },
  methods: {
    addaccessinfo() {
      axios
        .get("/api/addaccessinfo", {
          params: {
            accesstime: new Date().getTime()
          }
        })
        .then(res => {
         
        });
    },
    getaccessinfo() {
      axios.get("/api/getaccessinfo", {}).then(res => {
        this.access.series[0].data.splice(0, 1, {
          value: res.data.total,
          name: "历史访问量"
        });
        this.access.series[0].data.splice(1, 1, {
          value: res.data.weekaccess,
          name: "本周访问量"
        });
        this.access.series[0].data.splice(2, 1, {
          value: res.data.toddayaccess,
          name: "今日访问量"
        });
        this.dataready=true;
      });
    }
  },
  mounted() {
    this.addaccessinfo();
    this.getaccessinfo();
  },
  beforeCreate() {}
};
</script>
<style scoped>
.table {
  display: flex;
  width: 80%;
  margin: auto;
}
.titletext{
  font-size: 1rem;font-weight: 1000;
  margin-bottom:1rem;
}
.prjecttext{

  margin-bottom:1rem;
}
.importent{
  color:red;
}
</style>



