import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
    state:{
        tablist:[
            {
                path:"/",
                lable:"首页",
                name:"home",
            },
        ],
        currentMenu: {
            path:"/",
            lable:"首页",
            name:"home",
        },
        isCollapse:false
    },
        mutations:{
            changeCollapse(state){
                state.isCollapse=!state.isCollapse;
            },
            addtaglist(state,obj)
            {
                state.currentMenu=obj;
                var index  = state.tablist.findIndex((item)=>{
                    return item.name ===obj.name;
                } );
                if(index==-1)
                {
                    state.tablist.push(obj)
                }
            },
            removetag(state,obj){
                var index  = state.tablist.findIndex((item)=>{
                    return item.name ===obj.name;
                })
                state.tablist.splice(index,1);

            }
        }
})

export default store


