import Vue from 'vue'
import Router from 'vue-router'
import home from "../views/home"
import baseinfo from "../views/baseinfo"
import projectshow from "../views/projectshow"
import snake from "../views/snake"
import consume from "../views/consume_pleasure"
import custom_component from "../views/custom_components"
import customize_table from "../components/tool/customize_table"
import threedemo from "../views/threedemo"
import button from "../views/css_demo/button"
import audio_visualization from "../views/canvans/audio_visualization"
Vue.use(Router)
//解决  Avoided redundant navigation to current location: "XXXpath" 错误提示
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
    
  return originalPush.call(this, location).catch(err => err)
}

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      mode:'history',
      base:'/vue/',
      component: home,
      meta:{
        lable:"首页"
      } 
    },   {
      path: '/baseinfo',
      name: 'baseinfo',
      component: baseinfo,
      meta:{
        lable:"个人信息"
      } 
     
    },   {
      path: '/projectshow',
      name: 'projectshow',
      component: projectshow,
      meta:{
        lable:"项目展示"
      } 
    
    }, 
    {
      path: '/snake',
      name: 'snake',
      component: snake,
      meta:{
        lable:"贪吃蛇"
      } 
    }, 
    {
      path: '/consume',
      name: 'consume',
      component: consume,
      meta:{
        lable:"消消乐"
      } 
    },    
     {
      path: '/custom_component',
      name: 'custom_component',
      component: custom_component,
      meta:{
        lable:"自定义组件"
      } 
    }, 
    {
      path: '/customize_table',
      name: 'customize_table',
      component: customize_table,
      meta:{
        lable:"自定表格"
      } 
    }, 
    {
      path: '/threedemo',
      name: 'threedemo',
      component: threedemo,
      meta:{
        lable:"three.js_demo"
      } 
    }, 
    {
      path: '/button',
      name: 'button',
      component: button,
      meta:{
        lable:"按钮样例"
      } 
    }, 
    {
      path: '/audio_visualization',
      name: 'audio_visualization',
      component: audio_visualization,
      meta:{
        lable:"音频可视化"
      } 
    }, 




  ]
})



