<template>
  <div class="tag-list">
      <el-tag
      v-for="tags in tags"  
      :key="tags.name"
       :closable="tags.name!=='home'"
      :disable-transitions="false"
      @close.stop="handleClose(tags)"
      :effect="$route.name==tags.name?'dark':'plain'"
      style="margin-right:0.5rem;cursor: pointer;"
      class="tags"
    >
    <router-link :to=tags.path class="tags-link">
    {{ tags.lable }}</router-link> 
    </el-tag>
    
  
  </div>
</template>
<script>
    import router from "../router";
import store from "../store/tags"
export default {
  data() {
    return {
      tags: [
      ],
      currentMenu:{}
    };
  },
  methods:{handleClose(tags){
    var obj={}
    obj.name = tags.name;
    obj.path = tags.path;
    obj.lable = tags.lable;   
    store.commit('removetag',obj)
    //判断删除路由是否为当前显示的路由  是 则跳到最后一个路由  否则保持当前路由不变
    if(store.state.currentMenu.name==obj.name)
    {
      router.push(store.state.tablist[store.state.tablist.length-1]);
    }
    
   
  }},
  mounted(){
        this.tags = store.state.tablist;
        this.currentMenu = store.state.currentMenu;
  },

};
</script>
<style scoped>
  .tags-link{
    padding:0.4rem;
    box-sizing: border-box;
  }
</style>



