<template>
    <div class="content" >
        <div class="itemshows">
            <div class="line" >
                <show>
                <template #item><a class="streamer">button</a></template>
                <template #description>流光按钮</template>
                 </show>
            <show>
                <template #item><a class="share_box">
                    <div class="share_text">
                        <i class="el-icon-share">去分享</i>
                    </div>
                    <div class="share_index">
                        <i class="fa fa-qq " style="margin-left:4px;"></i>
                        <i class="fa fa-weixin "></i>
                        <i class="fa fa-weibo "></i>                  
                        <i class="fa fa-renren " style="margin-right:4px;"></i> 
                    </div>
                </a>
                </template>
                <template #description>去分享</template>
            </show>
            <show>
                <template #item>
                    <div class="masktext">
                        <span class="mask_text" >button</span>
                        <div class="mask_button" ref="mask"></div>
                    </div>
                </template>
                <template #description>马赛克按钮</template>
            </show>
            </div>
            <div class="line">
                <show>
                    <template #item><a class="flow">
                        <svg width="206px" height="66px " class="svg">
                            <rect class="shape"  width="206px" height="66px ">

                            </rect>
                        </svg>
                        <div class="flow_text">button</div>
                    </a>
                    </template>
                    <template #description>流边按钮</template>
                </show>
                <show>
                    <template #item><button class="embossed ">
                        浮雕
                    </button>
                    </template>
                    <template #description>射灯浮雕按钮</template>
                </show>
                <show>
                    <template #item><button class="ripples "  ref="ripples">
                        点我试试
                    <div class="issetripples"  ref="issetripples"></div>
                    </button>
                    </template>
                    <template #description>动态涟漪效果</template>
                </show>
            </div>
        </div>
    </div>
</template>

<script>
    import show from "../../components/template_style/showitems.vue"
    import 'font-awesome/css/font-awesome.min.css' 
    export default {
        data(){
            return {
           
            }
        },components:{show},methods:{
            difinemaskbuttonstyle(){
                let target  = this.$refs.mask;
                let width = target.clientWidth/25;

                let count = parseInt(target.clientHeight/width)
                for(let i = 0;i<25*count;i++)
                {
                    let span  = document.createElement('span');
                    span.style.width = width+'px'
                    span.style.height = width+'px'
                    span.style.transition = '0.2s linear '+Math.random()/2+'s';
                    target.appendChild(span);
                }
            },setripples(){
                
                $(document).on('click','.ripples',event=>{
                    
                        let childnode =  event.target.children;
                        // let target  =$('.issetripples');
                        let target  =this.$refs.issetripples
                        let dtarget = $(".issetripples");
                        target.innerHTML="";
                        // target.style.top
                        let x =  event.offsetX;
                        let y =  event.offsetY;
                        let style = document.createElement('style');
                        style.type = 'text/css';
                        style.rel = 'stylesheet';
                        style.appendChild(document.createTextNode(`.clickstyles { top:${y}px !important;left:${x}px !important; transform: scale(5);background: rgba(255,255,255,0.7);display:block !important;} .last{top:${y}px !important;left:${x}px !important;display:none ;}`));
                        target.appendChild(style);
                        setTimeout(() => {
                            dtarget.removeClass("clickstyles");
                        }, 400);
                        dtarget.addClass("clickstyles");
                        dtarget.addClass("last");
                })
            }
        },mounted(){
            this.difinemaskbuttonstyle();
            this.setripples();
        }
    }

</script>

<style scoped>  
    .issetripples{
        position: absolute;
        transition: 0.4s linear;
        width:30px;
        height:30px;
        background-color: rgb(255,255,255,0.6);

        border-radius: 50%;
    }
    .ripples{
        position: relative;
        width:200px;
        background-color: #19a9ed80;

        height:60px;
        border-radius: 30px;
        overflow: hidden;
        line-height: 60px;
        cursor: pointer;
    }
    /* 浮雕按钮 */
    .embossed{
        width:200px;
        height:60px;
        background: rgb(51,51,51);
        font-size: 26px;
        line-height: 60px;
        border:1px solid transparent;
        position: relative;
        box-shadow: inset 1px 1px 2px #000,inset -1px -1px 2px #fff;
        color:#333;
        text-shadow: 2px 2px 0 #808080;
        transition:0.3s linear 0.15s;
    }
    .embossed:hover::before{
        background: #f441a5;
        box-shadow: 0 0 10px #f441a5,
        0 0 20px #f441a5,
        0 0 30px #f441a5,
        0 0 40px #f441a5,
        0 0 50px #f441a5,
    }
    
    .embossed:hover{
        color:#fff;
        text-shadow: 0 0 10px #f441a5,
        0 5px 5px #000;
        box-shadow: inset 1px 1px 2px #000,inset -1px -1px 2px #f441a5;
    }
    .embossed::before{
        content: "";
        position: absolute;
        width:100px;
        height: 10px;
        top:0;
        left:50%;
        transform: translateX(-50%);
        border-radius: 0 0 50% 50%;
        filter:blur(5px);
        transition:0.3s
    }
    .svg{
        position: absolute;
        left:-3px;
        top:-3px;

    }


    .flow{
        width:200px; 
        height:60px;
        background: transparent;
        position: relative;
        background: rgba(0,0,0,0.2)
    }
    .flow:hover .shape{
        stroke: #3abe95;
        stroke-width: 6;
        stroke-dasharray: 50 0;
        stroke-dashoffset: 0;
    }
    .flow_text{
        width:200px;
        height:60px;
        border-radius: 30px;
        line-height: 60px;
        color:#fff;
        position: absolute;
        top:0px;
    }
    .shape{
        fill:none;
        stroke:#009ffd;
        stroke-width: 6;
        stroke-dasharray: 206 300;
        stroke-dashoffset: -270;
        transition: 1s ease;
    }
    .content{
        background:linear-gradient( to top,rgba(242 214 240),rgba(251 251 251));
        padding:1rem;
        box-sizing: border-box;
    }
    .itemshows{
        display: flex;
        justify-content: space-around;
        flex-direction:column;
    }
.streamer{
    width:200px;
    height:60px;
    line-height: 60px;
    text-align: center;
    text-decoration: none;
    font-size:24px;
    color:#fff;
    background: linear-gradient(to right,#03a9f4,#f441a5,#ffeb3b,#09a8f4);
    background-size:400%;
    border-radius: 50px;
    z-index:1;
    position: relative;
    cursor: pointer;
}
.streamer::before{
    content: "";
    position: absolute;
    top:-5px;
    left:-5px;
    right:-5px;
    bottom:-5px;
    background: linear-gradient(to right,#03a9f4,#f441a5,#ffeb3b,#09a8f4);
    background-size:400%;
    z-index:-1;
    filter: blur(80px);
}
.streamer:hover{
    animation: streamer 2s infinite;
    animation-timing-function: linear;
}
.streamer::after:hover{
    animation: streamer 2s infinite;
    animation-timing-function: linear;
}
@keyframes streamer{
    100%{
        background-position: -400% 0;
    }
}
.share_text{  
     width:200px;
    height:60px;
    line-height: 60px;
    background:black;
    border-radius: 30px;
    color:#fff;
    position: absolute;
    font-size: 18px;  
    transition: all 0.3s  linear;
    transition-delay: 0.3s;
    left:0%;
    z-index: 1;
}
.share_box{
    overflow: hidden;
    position: relative;
    width:200px;
    height:60px;
    line-height: 60px;
    border-radius: 30px;
    transition: all 0.3s  linear;
}
a{
    cursor: pointer;
}
.share_box:hover .share_text{
    left:-100%;
   
}
.share_box:hover{
    transform: scale(1.1);
}
.share_box:hover .share_index i{
    transform:translateX(0%);
    opacity: 1;
}
.share_index{
    width:200px;
    height:60px;
    background:#fff;
    border-radius: 30px;
    color:black;
    position: absolute;
    display: flex;
    padding:0px 2px;
    justify-content: space-around;
    align-items: center;
    font-size: 18px;
    z-index: -1;
}
.share_index i{
    transform:translateX(-300%);
    opacity: 0;
    transition: all 0.4s  linear;
}
.share_index i:nth-child(1){
    transition-delay: 0.3s;
}
.share_index i:nth-child(2){
    transition-delay: 0.2s;
}
.share_index i:nth-child(3){
    transition-delay: 0.1s;
}
.share_index i:nth-child(4){
    transition-delay: 0.1s;
}

.mask_button
{
    width:200px;
    height:64px;
    border-radius: 32px;
    position: absolute;
    top:0px;
    z-index: 1;
    

}
.masktext{
    width:200px;
    height:64px;
    background: black;
    line-height: 64px;
    color:#fff;
    position: relative;
    border-radius: 32px;
    overflow: hidden;
    transition:0.6s linear;
}

.masktext:hover{
    color:black;
    background: #fff;
}
.masktext:hover .mask_button >>> span {
    background: #fff;
}
.mask_text{
    position: relative;
    z-index: 100;
}
.mask_button >>> span{
    
    float:left;
    display: block;
}
.line{
    display: flex;
    justify-content: space-around   ;
    width: 100%;
    margin-bottom: 2rem;
}
</style>






