<template>
        <div class="conent" >
            这里是three.js   demo页面 <span style="margin-left:3rem;">别问,问就是官网抄的</span>
            <div id ="threedemo"></div>
        </div>
</template>

<style scoped>
.conent{
	width:100%;
	height:100%;
}
</style>
<script async src="https://unpkg.com/es-module-shims@1.3.6/dist/es-module-shims.js"></script>
<script>

	import * as THREE from 'three';

    import { GUI } from 'three/jsm/libs/lil-gui.module.min.js';
    import { OrbitControls } from 'three/jsm/controls/OrbitControls.js';
	
    
    export default{
        data(){
            return {

            }
        },mounted(){
            let controls, camera, scene, renderer;
            let textureEquirec, textureCube;
            let sphereMesh, sphereMaterial;
    
			init();
			animate();

			function init() {

				// CAMERAS

				camera = new THREE.PerspectiveCamera( 70, window.innerWidth / window.innerHeight, 1, 100000 );
				camera.position.set( 0, 0, 1000 );

				// SCENE
                scene = new THREE.Scene();
             
                const loader = new THREE.CubeTextureLoader();


				loader.setPath('http://localhost:5500/src/assets/textures/cube/Bridge2/');
		
				textureCube = loader.load( [ 'posx.jpg', 'negx.jpg', 'posy.jpg', 'negy.jpg', 'posz.jpg', 'negz.jpg' ] );
				textureCube.encoding = THREE.sRGBEncoding;
				const textureLoader = new THREE.TextureLoader();

				textureEquirec = textureLoader.load( 'http://localhost:5500/src/assets/textures/2294472375_24a3b8ef46_o.jpg.preview' );
				textureEquirec.mapping = THREE.EquirectangularReflectionMapping;
				textureEquirec.encoding = THREE.sRGBEncoding;

				scene.background = textureCube;
              
				const geometry = new THREE.IcosahedronGeometry( 400, 15 );
				sphereMaterial = new THREE.MeshBasicMaterial( { envMap: textureCube } );
				sphereMesh = new THREE.Mesh( geometry, sphereMaterial );
				scene.add( sphereMesh );


				
				renderer = new THREE.WebGLRenderer();
				renderer.setPixelRatio( window.devicePixelRatio );

				let renderdom = document.getElementsByClassName('conent')
	
				renderer.setSize( renderdom[0].offsetWidth, renderdom[0].offsetHeight);
				renderer.outputEncoding = THREE.sRGBEncoding;
				document.getElementById('threedemo').appendChild( renderer.domElement );


				controls = new OrbitControls( camera, renderer.domElement );
				controls.minDistance = 500;
				controls.maxDistance = 2500;

			
            }
			function animate() {

			requestAnimationFrame(animate);

			render();

			}

			function render() {

			camera.lookAt( scene.position );
			renderer.render( scene, camera );

			}

        }
    }
 




</script>


