<template>
  <div class="content">
    <div class="info">
      <h2>利用canvans实现简单音频可视化</h2>
     <input type="file" id="file" accept="audio">
     <audio id="audio" src=""></audio>
     <i class="el-icon-video-play icon" v-if="isplay"  @click="isplayfn(AudioBufferSourceNode)"></i>
     <i class="el-icon-video-pause icon " v-if="!isplay" @click="isplayfn(AudioBufferSourceNode)"></i>
    </div>
    <div class="audiocanvans-box">
        <canvas id="canvans" width="1000" height="300"></canvas>
    </div>
  </div>
</template>
<style scoped>
.icon{
  font-size: 1.6rem;
}


</style>

<script>
export default{
    data(){
            return {
                isplay:false,
                AudioBufferSourceNode:null,
            }
    },methods:{
       playFun(buffer,AudioBufferSourceNode,audioCtx,AnalyserNode,osc){
            AudioBufferSourceNode.buffer = buffer; // AudioBuffer数据赋值给buffer属性
            AudioBufferSourceNode.connect(audioCtx.destination); // 如果只是播放音频，这边就直接将AudioBufferSourceNode连接到AudioDestinationNode
            AudioBufferSourceNode.connect(AnalyserNode);  // 实现播放后，需要将bufferSourceNode连接到AnalyserNode，才能通过AnalyserNode获取后面可视化所需的数据
            AudioBufferSourceNode.loop = true;  // 循环播放，默认为false
            osc.connect(audioCtx.destination);
            AudioBufferSourceNode.start(0); // 开始播放音频
            osc.disconnect(audioCtx.destination);
            this.AudioBufferSourceNode=AudioBufferSourceNode;
        },isplayfn(AudioBufferSourceNode){
            console.log(AudioBufferSourceNode);
            this.isplay=!this.isplay;
            if(this.isplay)
            {
                this.AudioBufferSourceNode.stop();
            }
        }
    },mounted(){
        let musicfile = document.getElementById('file');
                let canvans = document.getElementById('canvans');
                let ctx = canvans.getContext("2d");
                let audio=document.getElementById("audio")
                let audioCtx = new (window.AudioContext || window.webkitAudioContext)();
                let osc = audioCtx.createOscillator();   
                var AudioBufferSourceNode = audioCtx.createBufferSource();
                var AnalyserNode = audioCtx.createAnalyser();
                
                musicfile.onchange=()=>{
                    
                    let file = musicfile.files[0];
                    audio.src= file.src;

                    let fr = new FileReader();
                    fr.readAsArrayBuffer(file);
                    fr.onload = e=>{
                        this.isplay=false;
                        if(AudioBufferSourceNode.buffer!=null) AudioBufferSourceNode.buffer=null;
                        AudioBufferSourceNode = audioCtx.createBufferSource();
                         //解码
                        audioCtx.decodeAudioData(e.target.result,buffer=>{
                            this.playFun(buffer,AudioBufferSourceNode,audioCtx,AnalyserNode,osc);
                        },err=>{
                            console.log(err)
                        })
                    }
                }
               
                var arr = new Uint8Array(AnalyserNode.frequencyBinCount);//用于存放音频数据的数组，其长度是fftsize的一半;
                requestAnimationFrame = window.requestAnimationFrame ||
                    window.webkitrequestAnimationFrame ||
                    window.mozrequestAnimationFrame;//兼容
                      
                      function render(){
                        AnalyserNode.getByteFrequencyData(arr);// 将音频频域数据复制到传入的Uint8Array数组
                        draw(arr); // 频域数据作为参数传入绘制函数draw
                        requestAnimationFrame(render);
                      }
                      requestAnimationFrame(render);
                      function  draw(arr){
                        //绘画  
                        ctx.clearRect(0,0,1000,1000); 
                        for(let i=0;i<1024;i++)
                        {
                            ctx.beginPath();
                            let  grad=ctx.createLinearGradient(0,0,300,0);
                            ctx.fillStyle =grad;
                            grad.addColorStop(0.3,"#C6FFDD");                  //定义渐变色颜色
                            grad.addColorStop(0.8,"#FBD786");
                            grad.addColorStop(1,"#F7797D");
                            var height=arr[i]/64*50;
                            ctx.fillRect(1*i,300-height,10 *0.1, height);
                            ctx.closePath();
                        }        

                    }
    },beforeDestroy(){
        if(this.AudioBufferSourceNode)
        this.AudioBufferSourceNode.stop();
    }

}



</script>



