<template>
  <div class="conent">
    <div class="info">
      <h2 style="margin-bottom:0px;">实现连连看小游戏</h2>
      <div class="dis">
        <span class="nandian">不足:消除动画,游戏音效</span>
        <span class="nandian" style="margin-right:1rem;"
          >技术难点:消除算法,消除路径绘制</span
        >
      </div>
      <div class="ctr" v-if="gamestart">
        <div style="font-weight:400;font-size:1rem;;margin-right:1rem" v-if="isnoconnect"><span style="color:red">没有可以相连的了,请点击刷新按钮</span></div>
        <div style="font-weight:400;font-size:1rem;;margin-right:1rem">剩余时间:<span style="color:red"> {{timetext}}</span></div>
        <div style="font-weight:400;font-size:1rem;;margin-right:1rem">当前得分:{{gamescore*Math.floor(itemslength/10)}}</div>
  
        <el-badge :value="searchnumber" type="info" >
                <i
            class="el-icon-search ctr-i"
            @click="searchbutton()"
            title="查找"
            ></i>
        </el-badge>
        <i
          class="el-icon-refresh-right ctr-i"
          title="打乱"
          @click="mix()"
          style="margin-left:1rem;"
          ref="refresh"
        ></i>
        <i
          class="el-icon-refresh ctr-i"
          title="重置"
          @click="reload()"
          style="margin-right: 1rem;margin-left:1rem;"
          ref="refresh"
        ></i>
      </div>
    </div>
    <div class="gamebox">
     
      <div class="gamebutton" ref="gamebutton">
        <div class="item" v-for="(item,index) in gameitems" :key="index"><div class="imgitem" v-if="item!=-1"><div :class=gamgstyleitems[item]></div></div></div>
        <!-- <div class="item" v-for="(item,index) in gamgstyleitems" :key="index"><div class="imgitem"><div :class=item></div></div></div> -->
      </div>
      <canvas class="gamecanvans" width="780" height="780" id="canvans"></canvas>
      <div class="choosenandu" ref="choosenandu">
        <h3>开始游戏</h3>
        <div class="cnandu" @click="choose(18)">
          <button class="nandubutton">简单</button>
        </div>
        <div class="cnandu" @click="choose(24)">
          <button class="nandubutton">普通</button>
        </div>
        <div class="cnandu" @click="choose(31)">
          <button class="nandubutton">困难</button>
        </div>
      </div>
    </div>
    <div class="foot" style="width:100%;height:5rem"></div>
  </div>
</template>

<style scoped>
    .cnandu .nandubutton:nth-child(1):hover {
  background: rgb(0, 143, 0);
}
.cnandu .nandubutton:nth-child(1) {
  background: rgb(3, 199, 3);
  
}
.info {
  width: 60%;
  display: flex;
  flex-direction: column;
}
.ctr{
  display: flex;
  align-items: center;
  justify-content: end;
  margin-bottom:1rem; 
}
.ctr-i {
  font-size: 2rem;
  float: right;

  cursor: pointer;
}
.nandubutton {
  width: 100%;
  height: 100%;
  color: #fff;
  cursor: pointer;
}
.cnandu {
  width: 40%;
  margin: auto;
  height: 20%;
}
.choosenandu {
  width: 100%;
  height: 100%;
  background: rgba(80, 80, 80, 0.1);
  position: absolute;
  left: 0px;
  top: 0px;
  display: flex;
  flex-direction: column;
  
}
    .gamecanvans{

        position: absolute;
        left:0px;
        top:0px;
        z-index: 100;
        pointer-events: none;
    }
   
img {
  border: 0px;
}
.imgitem {
  width: 100%;
  height: 100%;
  background-image: url("../assets/gameimg/items.png");
  background-size: 750%;
  background-position: 2px 0px;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  display: flex;
  /* border: 2px solid transparent; */
}
.item {
  width: 60px;
  height: 60px;
  border: 0px;
  margin:2px 2px;
}
.gamebutton {
  display: flex;
  flex-wrap: wrap;
  z-index: 10;
  display: none;
  width: 780px;
  height: 780px;
}
.gamebox {
  width: 780px;
  height: 780px;
  box-sizing: border-box;
  position: relative;
  margin: auto;
}

.info {
  width: 60%;
  display: flex;
  flex-direction: column;
  margin: auto;
}
.ctr {
  display: flex;
  align-items: center;
  justify-content: end;
  margin-bottom: 1rem;
}
.ctr-i {
  font-size: 2rem;
  float: right;

  cursor: pointer;
}
.content {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.nandian {
  font-size: 1rem;
  font-weight: 300;
  color: gray;
  float: right;
}
.dis {
  font-size: 1rem;
  font-weight: 300;
  float: right;
  margin-bottom: 1rem;
}
/* 选中的动画 */
.selected{
  margin:0px;
  border:2px solid red;
  
}
</style>
<script>
  import axios from "axios";
import '../style/consumegame.css'
export default {
  data() {
    return {
      land: [[]],
      //存放图片样式的数组
      gamgstyleitems: ['tree','ding','hand','fork','carrot','onions','grass','blood','cabbage','flower','bonfire','sword','apple','cotton','bucket','brush','hairball','gift','wheat','crab','rabbit','cuptea','ring','whitewine','mooncake','spell','book','gossip','quill','pinkmooncake','yellowmooncake','scissors'],
      //12*12
      itemslength:0,
      gameitems:[],
      issetlistener:false,
      oldselect:null,
      newselect:null,
      canvans:null,
      drawitems1:null,
      drawitems2:null,
      searchnumber:0,
      mixnumber:0,
      guanka:null,
      gametimer:null,
      timetext:"",
      gamescore:0,
      gamestart:false,
      isnoconnect:false,
      time:180,
    };
  },
  methods: {
    //初始化游戏
    init() {
      //确保难度不是随机的
     
      this.gameitems=[];
      for(let i =0;i<this.itemslength;i++)
      {
        this.gameitems.push(i);
      }
      for(let i=0;i<50-this.itemslength;i++)
      {
         this.gameitems.push(Math.round(Math.random()*this.itemslength));
      }
      //倍增确保成对

         this.gameitems.push(...this.gameitems);
      // console.log(this.gameitems);
      //随机打乱数组
      let length = this.gameitems.length;
      for(let i=length-1;i>0;i--)
      {
        //交换到最后一行
        let index  = Math.round(Math.random()*i);
        let temp  = this.gameitems[i]
        this.gameitems[i]  = this.gameitems[index];
        this.gameitems[index] = temp;
      }
      // this.gameitems[length-1]=-1; 测试 确保删除后的元素不能被点击
      // let set = new Set([...this.gameitems]);
      // console.log(set.size,set);
      var array=[]
      array.push(...new Array(12).fill(-1));
     
      for(let i=0;i<this.gameitems.length;i++)
      {
          if(i%10==0) array.push(-1);
          array.push(this.gameitems[i]);
          if(i%10==9) array.push(-1);
      }
      array.push(...new Array(12).fill(-1));
      // for(let i=0;i<30;i++)
      // {
      //   let index = Math.floor(Math.random()*100)+12;
      //   // console.log(index);
      //     array[index]=-1;
      // }
      clearInterval(this.gametimer);
      this.gametimer=setInterval(this.gametimectr,1000);
      this.gameitems=array;
      //设置监听事件
      this.setlistener();
      this.canvans = document.getElementById("canvans").getContext("2d");

    },begingame(){
        this.$refs.choosenandu.setAttribute("style", "display:none");
        this.$refs.gamebutton.setAttribute("style", "display:flex");
        this.gamestart=true;
        this.searchnumber=5;
        this.mixnumber=5;
        this.init();

    },reload(){
        this.$refs.choosenandu.setAttribute("style", "display:flex");
        this.$refs.gamebutton.setAttribute("style", "display:none");
        this.gameitems=[];
        this.gamescore=0;
        this.time=180;
        this.timetext="";
        this.gamestart=false;
        clearInterval(this.gametimer);

    },choose(val)
    {
        this.itemslength=val;
        this.begingame();
    },
    //搜索按钮
    searchbutton(){
    if(this.searchnumber>0)
    {
      this.searchnumber--;
      let result = this.search();
      $(".item").eq(result[0].o1).addClass ("selected");
      $(".item").eq(result[0].o2).addClass ("selected");
      setTimeout(() => {
        $(".item").eq(result[0].o1).removeClass ("selected");
      $(".item").eq(result[0].o2).removeClass ("selected");
      }, 2000);
    } 
    },mix(){
      let array=[];
      let gameitems = this.gameitems;
      let length  =this.gameitems.length;
      for(let i=0;i<length;i++)
      {
        if(gameitems[i]!=-1) 
        {
          array.push(i);
        }
      }
      length=array.length
      for(let i=length-1;i>0;i--)
      {
        let index = Math.round(Math.random()*i);
        let temp = gameitems[array[i]];
        gameitems[array[i]]=gameitems[array[index]];
        gameitems[array[index]]=temp;
      }
      this.gameitems=[...gameitems];
      if(this.search().length==0)
      {
        
      }else{
        this.isnoconnect=false;
      }
    },
    //搜索
    search(){
      
      //找出所有不为空的位置,然后组合判断
      let result = [];
      let array=[];
      let length = this.gameitems.length
      for(let i=0;i<length;i++)
      {
        if(this.gameitems[i]!=-1)
        {
          array.push(i);
        }
      }
      if(array.length==0)
      {
        alert("恭喜完成,进入下一关卡");
        this.time+=240;
        this.init();
        return [];
      }
      length=array.length;
      // console.log(array);
      for(let i=0;i<length;i++)
      {
        for(let j=i+1;j<length;j++)
        {
          //相对且可以消除
          let obj1={x:array[i]%12,y:Math.floor(array[i]/12)};
          let obj2={x:array[j]%12,y:Math.floor(array[j]/12)};
     
          if(this.gameitems[array[i]]==this.gameitems[array[j]]&&(this.isline(obj1,obj2)||this.isrow(obj1,obj2)||this.isquery(obj1,obj2)))
          {
            // console.log(this.gameitems[array[i]],this.gameitems[array[j]])
            result.push({o1:array[i],o2:array[j]});
          }
        }
      }
      if(result.length==0)
      { 
        this.isnoconnect=true;
      }else { if(this.isnoconnect==true) this.isnoconnect=false}


      // console.log(result)
        return result;
    },
    //设置点击监听事件
    setlistener(){
      //改变this指向 访问 vue中的函数对象
      let _this=this;
      if(!this.issetlistener)
      {
        $(document).on("click", ".item", function(event){
          // console.log(event.currentTarget)
          
        let x = $(".item").index(event.currentTarget) % 12;
        let y = Math.floor($(".item").index(event.currentTarget) / 12);

          _this.selecteditem(x+y*12)
          // console.log(x,y,x+y*10);
          // console.log(_this);
          });
      }
      //是否添加监听事件
     this.issetlistener=true;
      
      // $(".item").on("click",event => {
      //   console.log(event.currentTarget)
      //   let x = $(".item").index(event.currentTarget) % 10;
      //   let y = Math.floor($(".item").index(event.currentTarget) / 10);
      //   console.log(x, y);
      // })
    },selecteditem(val){

      if(this.gameitems[val]!=-1)
      {
        if(val == this.oldselect||this.oldselect==null)
        {
          this.oldselect=val;
        }else{
          this.newselect=val;
        }
      //  console.log(this.oldselect,this.newselect);
        this.drawseletitem(this.oldselect,this.newselect);
      }
        
    },
    //点击边框动画
    drawseletitem(val1,val2)
    {
      // console.log(val1,val2);
      //防止重复引用this
      if(val1){

        $(".item").eq(val1).addClass ("selected");
        // console.log("add1");
      }   
      if(val2){
        $(".item").eq(val2).addClass ("selected");
        // console.log("add2");
      } 
      //  console.log(obj1,obj2);
      //如果没有选中两个则不进行判断  防止初始值给第一个选中的   注释123
      if(this.newselect!=null){
        this.iseliminate(val1,val2)

      }else{
      }
    },
    //判断是否可以消除
    iseliminate(val1,val2){
      
      let gameitem = this.gameitems;
      let obj1={x:val1%12,y:Math.floor(val1/12)};
      let obj2={x:val2%12,y:Math.floor(val2/12)};
      //console.log(obj1,obj2);
      if(gameitem[val1]!=gameitem[val2])
      {
        $(".item").eq(val1).removeClass ("selected");
        //注释123
        this.oldselect=this.newselect;
        this.newselect=-1;
        return false;
      }else{
        //值相同时才进行判断
        // ||this.isquery(obj1,obj2)
        // console.log(obj1,obj2);
        let flag=false
        if(this.isline(obj1,obj2)||this.isrow(obj1,obj2)||(flag=this.isquery(obj1,obj2)))
        { 
          this.oldselect=null;
          this.gamescore++;
          this.newselect=null;
          let array=[...this.gameitems];
          array[val1]=-1;
          array[val2]=-1;
       
          if(flag)
          {
            this.drawline(obj1,obj2,"query",this.drawitems1,this.drawitems2);
          }else{
            this.drawline(obj1,obj2,"line");
          }
          setTimeout(() => {
            $(".item").eq(val1).removeClass ("selected");
            $(".item").eq(val2).removeClass ("selected");  
            this.canvans.clearRect(0,0,780,780);
            this.gameitems=array;
            if(this.search().length==0)
          {
          
          }
          }, 600);
         
          return true;
        }
        else{
          $(".item").eq(val1).removeClass ("selected");
          this.oldselect=null;
          this.newselect=null;
          return false;
        }
        
      }
    },
    //判断当前行能不能直接相连
    isline(val1,val2){
      if(val1.y!=val2.y)
      {
        return false;
      }
      //计算两块间所有元素是否都为空
      let array=[];
      if(val1.x<val2.x)
      {
        for(let i=val2.x-val1.x;i>1;i--)
        {
          array.push(this.gameitems[val1.x+i-1+val1.y*12]);
        }
      }else{
        for(let i=val1.x-val2.x;i>1;i--)
        {
          array.push(this.gameitems[val2.x+i-1+val2.y*12]);
        }
      }
      let length = array.length;
      // console.log(array);
      for(let i=0;i<array.length;i++)
      {
        if(array[i]==-1) length--;
      }
   
      return length==0?true:false;

    },
    //判断列能不能直接相连
    isrow(val1,val2){
      if(val1.x!=val2.x)
      {
        return false;
      }
      let array=[];
      if(val1.y<val2.y)
      {
        for(let i=val2.y-val1.y;i>1;i--)
        {
          array.push(this.gameitems[val1.x+(val1.y+i-1)*12]);
        }
      }else{
        for(let i=val1.y-val2.y;i>1;i--)
        {
          array.push(this.gameitems[val2.x+(val2.y+i-1)*12]);
        }
      }
      let length = array.length;
      for(let i=0;i<array.length;i++)
      {
        if(array[i]==-1) length--;
      }
      // console.log("isrow")
      
      return length==0?true:false;
      
    },
    isquery(val1,val2){
       let array1= this.get_directly_connected(val1);
       let array2= this.get_directly_connected(val2);
       array1=this.toObjxy(array1)
       array2=this.toObjxy(array2)
      //  console.log(array1,array2);
       for(let i = 0;i<array1.length;i++)
       {
        for(let j = 0;j<array2.length;j++)
        {
           if(this.isline(array1[i],array2[j])||this.isrow(array1[i],array2[j]))
           {
            this.drawitems1=array1[i];
            this.drawitems2=array2[j];
            return true;
           }
        }
       }
       return false;
    },
    //获取可以直接相连的点的数组
    get_directly_connected(val1){
      // console.log(val1);
      let array = [];
      //上下左右四个方向添加可以直接相连的点
      let  x = val1.x-1;
      let  y = val1.y;
      while(this.gameitems[x+y*12]==-1&&x>=0)
      {
        array.push(x+y*12);
        x-=1;
      }
      x=val1.x+1;
      y=val1.y;
      while(this.gameitems[x+y*12]==-1&&x<=11)
      {
        array.push(x+y*12);
        x+=1;
      }
      x=val1.x;
      y=val1.y-1;
      while(this.gameitems[x+y*12]==-1&&y>=0)
      {
        array.push(x+y*12);
        y-=1;
      }
      x=val1.x;
      y=val1.y+1;
      while(this.gameitems[x+y*12]==-1&&y<=11)
      {
        array.push(x+y*12);
        y+=1;
      }
      // console.log(array);
        return array;
    },
    toObjxy(array)
    {
      let result = [];
      for(let i=0;i<array.length;i++)
      {
        result.push({x:array[i]%12,y:Math.floor(array[i]/12)})
      }
      return result
    },drawline(val1,val2,type,...args)
    {
      let ctx = this.canvans;
        //规划路径,然后连线
      // console.log(...args);
        if(type=="line")
        {
            ctx.beginPath()
            ctx.strokeStyle ="red";
            ctx.lineWidth = 6;
            ctx.moveTo((val1.x)*64+33,(val1.y)*64+33)
            ctx.lineTo((val2.x)*64+33,(val2.y)*64+33)
            ctx.stroke();
            ctx.fill();
        } else{
            ctx.beginPath()
            ctx.strokeStyle ="red";
            ctx.lineWidth = 6;

            ctx.moveTo((val1.x)*64+33,(val1.y)*64+33)
            ctx.lineTo((args[0].x)*64+33,(args[0].y)*64+33)
            ctx.moveTo((val2.x)*64+33,(val2.y)*64+33)
            ctx.lineTo((args[1].x)*64+33,(args[1].y)*64+33)

            ctx.moveTo((args[0].x)*64+33,(args[0].y)*64+33)
            ctx.lineTo((args[1].x)*64+33,(args[1].y)*64+33)
            ctx.stroke();
            ctx.fill();
        }
    },gametimectr(){
      this.time--;
      if(this.time==0)
      {
        alert("时间到了游戏结束");
        clearInterval(this.gametimer);
        this.$refs.choosenandu.setAttribute("style", "display:flex");
        this.$refs.gamebutton.setAttribute("style", "display:none");
        this.time=180;
        this.addinfotoserver()
        this.gamescore=0;
       
      }
      let min = Math.floor(this.time/60);
      let sec = Math.floor(this.time%60)
      sec=sec<10?"0"+sec:sec;
      this.timetext=min+":"+sec;
    },addinfotoserver(){
      axios.get("/api/addconsumegameinfo",{
        params:{
          score:(this.gamescore*Math.floor(this.itemslength/10)),
          date:new Date().getTime(),
        }
      })
    }
  },
  created() {


  },beforeDestroy(){
    clearInterval(this.gametimer);
  },
  mounted() {
    //  console.log(  $(".imgitem"))


  }
  
};
</script>





