<template>
  <div>
  <div  class="content"
    ref="access_acle"
    style="width: 400px; height: 400px;" >

  </div>
  </div>
</template>
<style scoped></style>
<script>
let Echarts = require("echarts/lib/echarts"); //基础实例 注意不要使用import
require("echarts/lib/chart/pie"); //按需引入 bar = 柱状图

import {
  LegendComponent,
  TitleComponent,
  TooltipComponent
} from "echarts/components";
Echarts.use([TitleComponent, TooltipComponent, LegendComponent]);

export default {
  data() {
    return {
      chart: null
    };
  },
  methods: {
    init() {
      this.chart = Echarts.init(this.$refs.access_acle);
      this.chart.setOption(this.option);
    }
  },
  mounted() {
    this.init();
  },props:{
    option:Object
  }
};
</script>



