<template>
  <div class="content">
    <div class="info">
      <h2>利用canvans实现贪吃蛇小游戏</h2>
      <div class="dis">
        <span class="nandian"></span>
        <span class="nandian" style="margin-right:1rem;"
          ></span
        >
      </div>
      <div class="ctr">
        <div style="font-weight:400;font-size:1rem;;margin-right:1rem">当前体重:{{snake.body.length}}</div>
        <div style="font-weight:400;font-size:1rem;;margin-right:1rem">当前得分:{{(snake.body.length-4)*(this.snake.speed)}}</div>
        <div class="">选择🐍的颜色</div>
        <div class="block">
          <el-color-picker v-model="color1"> <span class="demonstration"></span></el-color-picker>
        </div>
        <el-switch
          v-model="iswall"
          active-text="墙体碰撞"
          inactive-text="穿墙术" style="margin-right:1rem;">
        </el-switch>
        <i
          class="el-icon-setting ctr-i"
          @click="setting()"
          title="游戏参数设置"
          style="margin-right:1rem;"
        ></i>
        <i
          class="el-icon-refresh ctr-i"
          title="重置"
          @click="reload()"
          style="margin-right: 1rem;"
          ref="refresh"
        ></i>
     
      </div>
    </div>
    <div class="gamecanvans">
      <canvas id="sanke" width="600" height="600"></canvas>
      <div class="choosenandu" ref="choosenandu">
        <h3>开始游戏</h3>
        <div class="cnandu" @click="choose(1)">
          <button class="nandubutton">简单</button>
        </div>
        <div class="cnandu" @click="choose(4)">
          <button class="nandubutton">普通</button>
        </div>
        <div class="cnandu" @click="choose(6)">
          <button class="nandubutton">困难</button>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
.cnandu .nandubutton:nth-child(1):hover {
  background: rgb(0, 143, 0);
}
.cnandu .nandubutton:nth-child(1) {
  background: rgb(3, 199, 3);
  
}
.info {
  width: 60%;
  display: flex;
  flex-direction: column;
}
.ctr{
  display: flex;
  align-items: center;
  justify-content: end;
  margin-bottom:1rem; 
}
.ctr-i {
  font-size: 2rem;
  float: right;

  cursor: pointer;
}
.nandubutton {
  width: 100%;
  height: 100%;
  color: #fff;
  cursor: pointer;
}
.cnandu {
  width: 40%;
  margin: auto;
  height: 20%;
}
.gamecanvans {
  width: 600px;
  height: 600px;
  position: relative;
}
.choosenandu {
  width: 100%;
  height: 100%;
  background: rgba(80, 80, 80, 0.1);
  position: absolute;
  left: 0px;
  top: 0px;
  display: flex;
  flex-direction: column;
  
}
.content {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.nandian {
  font-size: 1rem;
  font-weight: 300;
  color: gray;
  float: right;
}
.dis {
  font-size: 1rem;
  font-weight: 300;
  float: right;
  margin-bottom: 1rem;
}
</style>
<script>
  import axios from 'axios';
export default {
  
  data() {
    return {
      //🐍对象
      snake: {
        body: [],
        range: 0,
        speed:null,
        window: window
      },
      //游戏时钟
      gameinterval: null,
      //画布对象
      canvans: null,
      //平面
      land: null,
      //食物位置
      food: [],
      foodinterval: null,
      ke: 1,
      start:false,
      iswall:false,
      color1:"#FF0000"
    };
  },
  created() {},
  mounted() {
    this.canvans = document.getElementById("sanke").getContext("2d");
    this.$nextTick(() => {
      this.keyDown();
    });
  },
  methods: {
    reload() {
      //清空画布,时钟 初始化参数
      this.canvans.clearRect(0, 0, 1000, 1000);
       this.start=false;
       this.$refs.choosenandu.setAttribute("style", "display:flex");
       this.end();
    },
    choose(val) {
      this.snake.speed = val;
      this.$refs.choosenandu.setAttribute("style", "display:none");
      this.start=true;
      this.begingame();
    },
    begingame() {
      //随机生成🐍方向,起始位置
      if(!this.start)
      {
        alert("请选择游戏难度开始游戏");
        return 0;
      }
      this.init();
      this.foodinterval = setInterval(() => {
        this.addfood();
      }, 1000);
      this.gameinterval = setInterval(() => {
        this.canvans.clearRect(0, 0, 1000, 1000);
        this.draw();
      }, (10 - this.snake.speed) * 20);
    },
    //游戏绘画
    draw() {
      this.gamecontrol();
      let length = this.snake.body.length;
      //平面
      this.canvans.fillStyle = "#D3D3D3";
      this.canvans.fillRect(
       0,
        0,
          600,
          600
        );
        this.canvans.closePath();
      this.canvans.fill();
   
      //身体
      this.canvans.fillStyle = this.color1;
      for (let i = 0; i < length; i++) {
        this.canvans.fillRect(
          this.snake.body[i].x * 30 + 5,
          this.snake.body[i].y * 30 + 5,
          24,
          24
        );
      }
      this.canvans.closePath();
      this.canvans.fill();
      //眼睛
      this.canvans.fillStyle = "black";
      this.canvans.fillRect(
        this.snake.body[0].x * 30 + 12,
        this.snake.body[0].y * 30 + 12,
        10,
        10
      );
      this.canvans.closePath();
      this.canvans.fill();

      //食物
      if(this.food.length==1)
      {
        this.canvans.fillStyle = "green";
        this.canvans.fillRect(
            this.food[0].x * 30 + 6,
            this.food[0].y * 30 + 6,
            18,
            18
        );
        this.canvans.closePath();
        this.canvans.fill();
      }
      
    },
    //游戏控制层
    gamecontrol() {
     
      //蛇移动
      var length = this.snake.body.length;
      switch (this.snake.range) {
        case 1:
          this.snake.body.unshift({
            x: this.snake.body[0].x,
            y: this.snake.body[0].y - 1
          });
          this.snake.body.splice(length, 1);
          break;
        case 2:
          this.snake.body.unshift({
            x: this.snake.body[0].x,
            y: this.snake.body[0].y + 1
          });
          this.snake.body.splice(length, 1);
          break;
        case 3:
          this.snake.body.unshift({
            x: this.snake.body[0].x - 1,
            y: this.snake.body[0].y
          });
          this.snake.body.splice(length, 1);
          break;
        case 4:
          this.snake.body.unshift({
            x: this.snake.body[0].x + 1,
            y: this.snake.body[0].y
          });
          this.snake.body.splice(length, 1);
          break;
      }
      this.ke = 1;
      //碰撞检测
      let index = this.snake.body.slice(1).findIndex((item)=>{return JSON.stringify(item)==JSON.stringify(this.snake.body[0])})
      if(this.iswall==false)
      { 
          if(this.snake.body[0].x>19||this.snake.body[0].x<0)
          {    
            this.snake.body[0].x=this.snake.body[0].x<0?20+this.snake.body[0].x:this.snake.body[0].x;
            this.snake.body[0].x%=20;
          
          }
          if(this.snake.body[0].y>19||this.snake.body[0].y<0)
          {
            this.snake.body[0].y=this.snake.body[0].y<0?20+this.snake.body[0].y:this.snake.body[0].y;
            this.snake.body[0].y%=20;
          }
      }
      else{
        if(this.snake.body[0].x>19||this.snake.body[0].x<0||this.snake.body[0].y>19||this.snake.body[0].y<0)
          {    
            alert("撞墙啦")
            this.addinfotoserver()
            this.end();
          
          }
      }
     if(index!=-1)
     {
        clearInterval(this.gameinterval);
        this.addinfotoserver();
        this.end();
        alert("把自己咬了");
     }
     //食物到尾部才加长度
      if(this.food.length==1&&this.snake.body[0].x==this.food[0].x&&this.snake.body[0].y==this.food[0].y)
      {
        this.snake.body.unshift(this.food[0]);
        // console.log( this.snake.body);
        this.food.splice(0,1);
      }
    },
    //初始化🐍对象
    init() {
        this.snake.body.length=0;
      this.snake.range = Math.ceil(Math.random()*4);
      //在里面的网格中随机生成 头
      this.snake.body.push({
        x: Math.ceil(Math.random() * 4) + 3,
        y: Math.ceil(Math.random() * 4) + 3
      });

      switch (this.snake.range) {
        //方向朝上
        case 1:
          this.snake.body.push({
            x: this.snake.body[0].x,
            y: this.snake.body[0].y + 1
          });
          this.snake.body.push({
            x: this.snake.body[0].x,
            y: this.snake.body[0].y + 2
          });
          this.snake.body.push({
            x: this.snake.body[0].x,
            y: this.snake.body[0].y + 3
          });
          break;
        case 2:
          this.snake.body.push({
            x: this.snake.body[0].x,
            y: this.snake.body[0].y - 1
          });
          this.snake.body.push({
            x: this.snake.body[0].x,
            y: this.snake.body[0].y - 2
          });
          this.snake.body.push({
            x: this.snake.body[0].x,
            y: this.snake.body[0].y - 3
          });
          break;
        case 3:
          this.snake.body.push({
            x: this.snake.body[0].x + 1,
            y: this.snake.body[0].y
          });
          this.snake.body.push({
            x: this.snake.body[0].x + 2,
            y: this.snake.body[0].y
          });
          this.snake.body.push({
            x: this.snake.body[0].x + 3,
            y: this.snake.body[0].y
          });
          break;
        case 4:
          this.snake.body.push({
            x: this.snake.body[0].x - 1,
            y: this.snake.body[0].y
          });
          this.snake.body.push({
            x: this.snake.body[0].x - 2,
            y: this.snake.body[0].y
          });
          this.snake.body.push({
            x: this.snake.body[0].x - 3,
            y: this.snake.body[0].y
          });
      }
      // this.snake.body.forEach((item)=>{
      //     console.log(item.x,item.y);
      //   })
      //   console.log(this.snake.range);
    },
    end() {
      clearInterval(this.gameinterval);
      clearInterval(this.foodinterval);
    },setting(){
        clearInterval(this.gameinterval);
      clearInterval(this.foodinterval);
    },
    keyDown() {
      let that = this;
      document.onkeydown = e => {
        //事件对象兼容
        let e1 =
          e || event || window.event || arguments.callee.caller.arguments[0];
        if (e1 && e1.keyCode == 87 && this.ke == 1) {
          //del键，其他按键对应数值，打印查看即可
          if (this.snake.range != 2) {
            this.snake.range = 1;
            this.ke = 0;
          }
        } else if (e1 && e1.keyCode == 83) {
          if (this.snake.range != 1 && this.ke == 1) {
            this.snake.range = 2;
            this.ke = 0;
          }
        } else if (e1 && e1.keyCode == 65 && this.ke == 1) {
          if (this.snake.range != 4 && this.ke == 1) {
            this.snake.range = 3;
            this.ke = 0;
          }
        } else if (e1 && e1.keyCode == 68 && this.ke == 1) {
          if (this.snake.range != 3 && this.ke == 1) {
            this.snake.range = 4;
            this.ke = 0;
          }
        }
      };
    },
    addfood() {
      let notcontent = [];
      if (this.food.length < 1) {
        for (let i = 0; i < 20; i++) {
          for (let j = 0; j < 20; j++) {
            let obj = { x: i, y: j };
            var index = this.snake.body.findIndex(item => {
              return JSON.stringify(obj) == JSON.stringify(item);
            });
            if (index == -1) {
              notcontent.push(obj);
            }
          }
        }
        this.food.push(
          notcontent[Math.floor(Math.random() * notcontent.length - 1)]
        );
      }
    },addinfotoserver(){
      axios.get("/api/addsnakegameinfo",{
                    params:{
                        score:(this.snake.body.length-4)*(this.snake.speed),
                        date:new Date().getTime(),
                        
                    }
                }).then((res)=>{
          console.log(res);
        })
    }
  },beforeDestroy(){
    this.end();
  }
};
</script>



